"use client";

import { useState } from "react";

import AuthUserManager from "@/app/components/AuthUserManager";
import CardPackManager from "@/app/components/CardPackManager";
import DeckManager from "@/app/components/DeckManager";
import SettingsProfileForm from "@/app/components/SettingsProfileForm";

type CardPack = {
  id: number;
  name: string;
  releaseDate: string;
};

type Deck = {
  id: number;
  name: string;
  deckClass: string;
  cardPack: CardPack;
};

type UserRow = {
  id: number;
  name: string;
  role: "ADMIN" | "MEMBER";
  createdAt: string;
};

type Props = {
  profileName: string;
  cardPacks: CardPack[];
  decks: Deck[];
  users: UserRow[];
  isAdmin: boolean;
  currentUserId?: number;
};

type Tab = "profile" | "cardPacks" | "decks" | "users";

const tabs: { id: Tab; label: string }[] = [
  { id: "profile", label: "プロフィール" },
  { id: "cardPacks", label: "カードパック" },
  { id: "decks", label: "デッキ" },
  { id: "users", label: "ユーザー" },
];

export default function SettingsHub({
  profileName,
  cardPacks,
  decks,
  users,
  isAdmin,
  currentUserId,
}: Props) {
  const [activeTab, setActiveTab] = useState<Tab>("profile");

  return (
    <div className="space-y-6">
      <section className="rounded-2xl border border-zinc-200 bg-white p-6 shadow-sm">
        <div>
          <p className="text-xs font-semibold uppercase tracking-[0.3em] text-zinc-400">
            Settings
          </p>
          <h2 className="mt-2 text-2xl font-semibold text-zinc-900">
            マイページ
          </h2>
        </div>
        <div className="mt-6 flex flex-wrap gap-2">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              type="button"
              onClick={() => setActiveTab(tab.id)}
              className={`rounded-full border px-4 py-2 text-xs font-semibold transition ${
                activeTab === tab.id
                  ? "border-zinc-900 bg-zinc-900 text-white"
                  : "border-zinc-200 text-zinc-600 hover:bg-zinc-100"
              }`}
            >
              {tab.label}
            </button>
          ))}
        </div>
      </section>

      {activeTab === "profile" && (
        <SettingsProfileForm initialName={profileName} />
      )}
      {activeTab === "cardPacks" && (
        <CardPackManager cardPacks={cardPacks} />
      )}
      {activeTab === "decks" && (
        <DeckManager cardPacks={cardPacks} decks={decks} />
      )}
      {activeTab === "users" && (
        <AuthUserManager
          users={users}
          isAdmin={isAdmin}
          currentUserId={currentUserId}
        />
      )}
    </div>
  );
}
